.container{
    display: grid;
    grid-template-columns: 50% 50%;
}
.elmImage{
    width: 100%;
    height:100vh;
}
.formContainer{
    width: 50%;
    margin-top: 60px;
    margin-left: 25%;
    text-align: right;
}
.buttonDiv{
    display: flex;
    justify-content: center;
    min-width: 100%;
}
.maleButton, .femaleButton{
    width: 100%;
    height: 45px;
    margin-top: 6px;
    background-color: white;
    border: 1px solid lightgray;
    font-size: 20px;
    color: gray;
    cursor: pointer;
}
.femaleButton{
    margin-right: 10px;
}
.checkButton{
    padding: 6px;
    background-color: white;
    border: 1px solid gray;
    cursor: pointer;
    position: absolute;
    right: 0px;
}
.termsConditionsDiv{
    position: relative;
    padding-right: 20px;
    font-size: 14px;
    font-weight: 600;
}
.signupButton{
    width: 100%;
    height: 55px;
    font-size: 20px;
    background-color: rgb(46, 49, 146);
    color: white;
    border: none;
    cursor: pointer;
}
.warning{
    color: white;
    font-size: 13px;
    direction: rtl;
    margin-top: 2px;
    font-weight: 100;
}
.activeButton{
    border: 1px solid blue;
    color: blue;
    background-color: #F0F1F9;
}